import { VentasContext } from '../../Context/Ventas';
import Cliente from '../../Models/Cliente';
import FilaProto from '../Prototipos/FilaProto';
class FilaVentas extends FilaProto {
    tooltipCliente() {
        let cli: Cliente = this.context.findCliente(this.props.data.rif);
        if (cli)
            return "<h9 style='color: #5bc0de'>DATOS DE CONTACTO</h9><br />" +
                "<b>PERSONA DE CONTACTO:</b><br />" +
                cli.contacto + "<br />" +
                "<b>CORREO ELECTRONICO:</b><br />" +
                cli.correo + "<br />" +
                "<b>TELEFONO:</b><br />" +
                cli.telefono
        return '';
    }
}
FilaVentas.contextType = VentasContext;
export default FilaVentas