import React from "react";
import { Accordion, Button, Card, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import ReactTooltip from "react-tooltip";
import { VentasContext, VentasType } from "../../../Context/Ventas";
import { conCotizacion, Cotizacion, CotizacionNull, sentCotizacion } from "../../../Models/Factura";
import Condiciones from "../../../Utiles/Componentes/Condiciones";
import CreateOperacion from "../../../Utiles/Componentes/CreateOperacion";
import { numberFormat } from "../../../Utiles/Formatos";
import ClienteSelector from "../../Clientes/Selector";
import AuxiliarCotizacion from "./AuxiliarCotizacion";
class NuevaCotizacion extends CreateOperacion {
    tasa = true;
    con = conCotizacion();
    title = 'COTIZACION'
    msn = 'LA COTIZACION';
    constructor(p: any) {
        super(p);
        this.state = { ...CotizacionNull, procesar: false };
        this.tipo = 'Cotizacion';
    }
    setCliente(p: string) {
        this.setState({ cod_cliente: p });
    }
    Top() {
        const data = this.getData() as Cotizacion;
        const { tasa } = this.context as VentasType;
        return <>
            <ClienteSelector Cliente={data.cod_cliente} add={this.setCliente.bind(this)} />
            <Accordion>
                <Accordion.Toggle eventKey='0' as={Button} variant='primary'>DATOS OPCIONALES</Accordion.Toggle>
                <div className='float-right mr-3 mt-1'><b><span>VALOR DEL DOLAR:</span> {numberFormat(tasa)}</b> </div>
                <Accordion.Collapse eventKey='0'><Card>
                    <Card.Body>
                        <Row>
                            <Condiciones data={data} setData={this.setState} />
                        </Row>
                    </Card.Body>
                </Card>
                </Accordion.Collapse>
            </Accordion>
        </>
    }
    PDF() {
        const { dolar, codigo } = this.getData() as Cotizacion;
        return <Card id="tabla">
            <ReactTooltip />
            <Button style={{ marginRight: "15px" }} className="btn-block" onClick={() => this.setState({ dolar: !dolar })}
                data-tip={dolar ? "CAMBIAR A BOLIVARES" : "CAMBIAR A DOLARES"} variant={dolar ? "warning" : "primary"}>
                {dolar ? <><span className="fa fa-usd" />VISTA EN DOLARES</> : <><b>BS</b> VISTA EN BOLIVARES</>}
            </Button>
            <iframe src={"/api/Cotizacion/" + (dolar ? 'PDFD' : 'PDF') + "/" + codigo}
                width='100%' height='700wh' title="print" scrolling='no' frameBorder='0' />
        </Card>;
    }
    Productos() {
        this.dolar();
        return <AuxiliarCotizacion key="auxCotizacion" setData={this.setState} data={this.state} />
    }
    Procesar() {
        let procesar = true;
        if (this.state.cod_cliente === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } procesar = this.validacionGenerica(procesar);
        if (procesar)
            this.con.nuevo(sentCotizacion(this.getData(), this.context.impuesto), data => {
                this.setState({ codigo: data })
            });
    }
    dolar() {
        const { tasa } = this.context as VentasType;
        if ((tasa !== 0) && this.tasa) {
            this.setState({ dolar: this.context.dolar });
            this.tasa = false;
        }
    }
}
NuevaCotizacion.contextType = VentasContext;
export default NuevaCotizacion;