import React, { useContext } from 'react';
import { Route } from "react-router-dom"
import ListAjustes from '../App/Ajustes';
import NuevaAjuste from '../App/Ajustes/Nuevo';
import ListaProductos from '../App/Productos';
import ProductosProvider from '../Context/Producto';
import { SeccionContext } from '../Context/Seccion';
import UnAutorization from '../Utiles/Componentes/UnAutorization';
export default function MasterProductos() {
    return (<ProductosProvider>
        <Route path="/producto" exact component={Productos} />
        <Route path="/producto/ajuste" component={Ajuste} />
    </ProductosProvider>);
}
function Productos() {
    const contex = useContext(SeccionContext);
    if (contex?.data.Permisos.some(p => p === 'PRODUCTO'))
        return <ListaProductos />
    return <UnAutorization />
}
function Ajuste() {
    const contex = useContext(SeccionContext);
    if (contex?.data.Permisos.some(p => p === 'AJUSTE'))
        return <>
            <Route path="/producto/ajuste/lista" component={ListAjustes} />
            <Route path="/producto/ajuste/nuevo" component={NuevaAjuste} />
        </>
    return <UnAutorization />
}