import React from "react"
import { ControlText } from '../../Utiles/FormControl'
import { Button, Card, Col, FormGroup } from 'react-bootstrap'
import "./Login.css";
import { ConConfig } from "../../Models/Config";
import { SeccionContext } from '../../Context/Seccion'
import { toast, ToastContainer } from "react-toastify";

class Login extends React.Component<{ update: () => void }, { correo: string, clave: string }> {
    state = { correo: "", clave: "" };
    logo = '/logos/logo.svg';
    render() {
        const { correo, clave } = this.state;
        return <div className="login-layoud" style={{ backgroundImage: 'url("/logos/fondo con arbol.jpg")' }}>
            <div className="login-box">
                <ToastContainer position='bottom-right' />
                <Card border="primary" className="card-outline" style={{ backgroundColor: "rgba(255, 255, 255, 0.75)" }} >
                    <Card.Header className="text-center">
                        <Card.Img variant="top" src={this.logo} alt="ImgLogin" />
                    </Card.Header>
                    <Card.Body>
                        <Col as={FormGroup} xs={12}>
                            <ControlText before="CORREO" maxLength={150} value={correo} update='correo' setData={this.setState.bind(this)} />
                        </Col>
                        <Col as={FormGroup} xs={12}>
                            <ControlText before="CLAVE" maxLength={150} value={clave} update='clave' setData={this.setState.bind(this)} type="password" />
                        </Col>
                        <Button onClick={this.login.bind(this)} variant="primary" block>ENTRAR</Button>
                    </Card.Body>
                </Card>
            </div>
        </div>
    }
    login() {
        const con = ConConfig('');
        con.post('/api/Auth/login', this.state, (data) => {
            localStorage.setItem('Authorization', data.token);
            this.context.user();
        }, e => {
            toast(e.response.error, { type: toast.TYPE.ERROR })
        })
    }
    async Logo() {
        try {
            const rest = await fetch('/api/Auth/getConfig')
            const data = await rest.json();
            this.logo = '/public/imagenes/' + await data.logo;
            this.forceUpdate();
        } catch (e) {
            console.log(e);
        }
    }
    componentDidMount() {
        this.Logo();
    }
}
Login.contextType = SeccionContext;
export default Login;