import React from 'react';
import ListaCompra from '../App/Compra';
import NuevaCompra from '../App/Compra/Nuevo';
import ListaOrden from '../App/Orden';
import NuevaOrden from '../App/Orden/Nuevo';
import { Route } from "react-router-dom"
import ComprasProvider from '../Context/Compras'
import ListaProveedores from '../App/proveedores';
import { useContext } from 'react';
import { SeccionContext } from '../Context/Seccion';
import UnAutorization from '../Utiles/Componentes/UnAutorization';

export default function MasterCompras() {
    return (<ComprasProvider>
        <Route path="/orden" component={Orden} />
        <Route path="/compra" component={Compras} />
        <Route path="/proveedores" component={Proveedor} />
    </ComprasProvider>);
}
function Orden() {
    const contex = useContext(SeccionContext);
    if (contex?.data.Permisos.some(p => p === 'ORDEN'))
        return <>
            <Route path="/orden/lista" component={ListaOrden} />
            <Route path="/orden/nueva" component={NuevaOrden} />
        </>
    return <UnAutorization />
}
function Compras() {
    const contex = useContext(SeccionContext);
    if (contex?.data.Permisos.some(p => p === 'COMPRA'))
        return <>
            <Route path="/compra/lista" component={ListaCompra} />
            <Route path="/compra/nueva" component={NuevaCompra} />
        </>
    return <UnAutorization />
}
function Proveedor() {
    const contex = useContext(SeccionContext);
    if (contex?.data.Permisos.some(p => p === 'PROVEEDOR'))
        return <ListaProveedores />
    return <UnAutorization />
}