import React from 'react'
import { Button, Modal, Row, Col, InputGroup, FormGroup, FormControl, FormLabel } from 'react-bootstrap'

import Cliente, { ClienteNull, conCliente } from '../../Models/Cliente'
import { ControlArea, ControlText } from '../../Utiles/FormControl'
import { InputNumber } from '../../Utiles/Inputs'
interface Props {
    open: boolean
    cliente: Cliente | null
    title: string
    close: () => void
    add?: (c: Cliente) => void
}
interface State extends Cliente {
    activeError: boolean;
}
export default class FormCliente extends React.Component<Props, State> {
    error: any = [];
    con = conCliente();
    constructor(p: Props) {
        super(p)
        this.setState = this.setState.bind(this);
        let state = p.cliente;
        if (state === null) state = ClienteNull;
        this.state = { ...state, activeError: false };
    }
    render() {
        let { setState, state } = this
        return <Modal animation show={this.props.open} size='lg' centered onHide={this.props.close}>
            <Modal.Header className='bg-primary' closeButton>
                <h4 className='mr-auto'>{this.props.title}</h4>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <Col as={FormGroup} xs={12}>
                        <ControlText upper mask='A-99999999-9' maxLength={20}
                            before="RIF" value={state.codigo} setData={setState} update="codigo" disabled={(this.props.cliente !== null)}  error={this.error} />
                    </Col>
                    <Col as={FormGroup} hasValidation={this.error['nivel'] ? true : false} xs={12}>
                        <ControlText error={this.error} upper before="RAZON SOCIAL" maxLength={150}
                            value={state.nombre} setData={setState} update="nombre" />
                    </Col>
                    <Col as={FormGroup}  hasValidation={this.error['nivel'] ? true : false} xs={12}>
                        <ControlText error={this.error} upper before="CORREO" maxLength={150}
                            value={state.correo} setData={setState} update="correo" />
                    </Col>
                    <Col as={FormGroup}   xs={12}>
                        <ControlText  upper maxLength={120}
                            before="TEL√âFONO" value={state.telefono} setData={setState} update="telefono" error={this.error} />
                    </Col>
                    <Col as={FormGroup}  xs={12}>
                        <ControlText  upper before="PERSONA DE CONTACTO"
                            maxLength={50} value={state.contacto} setData={setState} update="contacto" error={this.error} />
                    </Col>
                    <Col as={FormGroup} xs={12}>
                        <InputGroup style={{ width: '100%' }} className="mb-3">
                            <InputGroup.Prepend>
                                <InputGroup.Text>TIPO DE CONTRIBUYENTE</InputGroup.Text>
                            </InputGroup.Prepend>
                            <FormControl as='select' style={{ width: '120px' }} onChange={this.setContribuyente.bind(this)} value={state.tipo_contribuyente}>
                                <option value="ORDINARIO">ORDINARIO</option>
                                <option value="ESPECIAL">ESPECIAL</option>
                            </FormControl>
                            <InputNumber value={state.retencion} setData={setState} update="retencion" step={0.01}
                                disabled={(state.tipo_contribuyente !== 'ESPECIAL')} />
                        </InputGroup>
                    </Col>
                    <Col as={FormGroup} hasValidation={this.error['nivel'] ? true : false} xs={12}>
                        <FormLabel>DIRECCION</FormLabel>
                        <ControlArea error={this.error} upper value={state.direccion} setData={setState} update="direccion" maxLength={300} />
                    </Col>
                </Row>
            </Modal.Body>
            <Modal.Footer>
                <Button className="float-right" onClick={this.get.bind(this)} variant="primary">
                    <span className="fa fa-floppy-disk" /> GUARDAR
                </Button>
            </Modal.Footer>
        </Modal>
    }
    isInvalid(s: string) {
        if (s === "")
            return true
        return false
    }
    get() {
        this.error = [];
        let procesar = true;
        if (this.state.codigo === "") {
            this.error['codigo'] = 'DEBE COLOCAR EL DOCUMENTO LEGAL DEL CLIENTE';
            procesar = false;
        } if (this.state.nombre === "") {
            this.error['nombre'] = 'DEBE COLOCAR LA RAZON SOCIAL';
            procesar = false;
        } if (this.state.telefono === "") {
            this.error['telefono'] = 'DEBE COLOCAR NUMERO DE TELEFONO';
            procesar = false;
        } if (this.state.direccion === "") {
            this.error['direccion'] = 'DEBE COLOCAR UNA DIRECCION';
            procesar = false;
        } if (this.state.contacto === "") {
            this.error['contacto'] = 'DEBE COLOCAR LA PERSONA DE CONTACTO';
            procesar = false;
        }this.forceUpdate();
        if (procesar) {
            if (this.props.cliente === null) this.con.nuevo(this.state, this.action.bind(this))
            else this.con.actualizar(this.props.cliente.codigo, this.state, this.action.bind(this))
    }}
    action(data: any) {
        if (this.props.add) this.props.add(data);
        if (this.props.cliente === null) this.setState(ClienteNull);
        this.props.close();
    }
    setContribuyente(Rol: React.ChangeEvent<HTMLSelectElement>) {
        let value = Rol.currentTarget.value as string;
        this.setState({ tipo_contribuyente: value });

    }
}