import { Conexion } from "../Config/Coneccion";
import { Producto } from "./Productos";
export interface Detalles extends Producto {
    unidades: number,
    comentario: string,
    precio: number,
}
export interface GeneralFila {
    codigo: number
    rif: string
    fecha: string
    nombre: string
    tasa: number
    monto: number
    usuario: number
    status: 0 | 1 | 2
    detalles: string[]
}
export interface GeneralData {
    id_nota?: number
    id_cotizacion?: number
    codigo: number,
    nota: string,
    fecha: string,
    subtotal: number,
    total: number,
    impuesto: number,
    tasa: number
    dolar?: boolean,
    detalles: Detalles[]
}
export interface CondicionesData extends GeneralData {
    forma_pago: string
    tiempo_entrega: string
    validez: string
}
export interface Impreciones {
    margen_sup: number,
    margen_inf: number,
    margen_izq: number,
    margen_der: number,
    version: string,
    tipo_papel: string,
    encabezado: boolean,
    piepagina: boolean
}
export var imprecionNull: Impreciones = {
    margen_sup: 15,
    margen_inf: 15,
    margen_izq: 15,
    margen_der: 15,
    version: 'nuevo',
    encabezado: true,
    piepagina: true,
    tipo_papel: 'LETTER'
}
export var generalNull: GeneralData = {
    codigo: 0,
    nota: '',
    fecha: '',
    subtotal: 0,
    total: 0,
    impuesto: 0,
    tasa: 0,
    detalles: []
}
export var condicionesNull: CondicionesData = {
    ...generalNull,
    forma_pago: '',
    tiempo_entrega: '',
    validez: '',
}

export function getSubtotal(d: Detalles[]) {
    let subtotal = 0;
    d.forEach(e => {
        let { precio, unidades } = e;
        subtotal += precio * unidades;
    })
    return subtotal;
}
export function getCosto(d: Detalles[]) {
    let subtotal = 0;
    d.forEach(e => {
        let { costo, unidades } = e;
        subtotal += costo * unidades;
    })
    return subtotal;
}
export function sentDetalles(d: Detalles[]) {
    return d.map(e => {
        let { codigo, precio, unidades, comentario } = e;
        return { codigo, precio, unidades, comentario };
    })
}

export type tipoData = 'Cotizacion' | 'Orden' | 'Nota' | 'Factura' | 'Compra';

export interface Moneda {
    codigo: number
    descripcion: string
    estatus: number
}

class MonedaConexion extends Conexion {
    clase = 'Moneda';
    persistencia = 1000;
}
export function conMoneda() {
    return new MonedaConexion();
}
export interface Estado {
    status: 1 | 2 | 3
    hora: string
}
export const EstadoNull: Estado = { status: 1, hora: 'CARGADO DESDE CACHE' }

export function arrayToFilaGeneral(f: any[]): GeneralFila[] {
    return f.map(a => {
        return {
            codigo: a[0],
            rif: a[1],
            nombre: a[2],
            fecha: a[3],
            monto: a[4],
            usuario: a[5],
            status: a[6],
            detalles: a[7],
            tasa: a[8] ? a[8] : 0
        }
    })
}