import React from 'react';
import User from './User';
interface Props {
    update: () => void
}
export default class Header extends React.Component<Props> {
    render() {
        return (<nav className="main-header navbar navbar-expand navbar-success navbar-light">
            <ul className="navbar-nav">
                <li className="nav-item">
                    <div className="nav-link" onClick={this.open.bind(this)}><i className="fa fa-bars"></i></div>
                </li>
            </ul>
            <ul className="navbar-nav ml-auto">
                <User/>
            </ul>
        </nav >)
    }

    open() {
        let body = document.getElementsByTagName('body')[0];
        let data = localStorage.getItem('menu');
        if (data === 'open') {
            localStorage.setItem('menu', 'cerrado')
            data = 'cerrado'
        } else {
            localStorage.setItem('menu', 'open')
            data = 'open'
        }
        if (body) {
            if (data === 'cerrado')
                body.classList.add('sidebar-collapse')
            else
                body.classList.remove('sidebar-collapse')
        }
        this.props.update();
    }

    componentDidMount() {
        let body = document.getElementsByTagName('body')[0];
        let data = localStorage.getItem('menu');
        if (!data) {
            data = "open";
            localStorage.setItem('menu', 'open')
        }
        if (body) {
            if (data === 'cerrado')
                body.classList.add('sidebar-collapse')
            else
                body.classList.remove('sidebar-collapse')
        }
    }
}