import React, { useContext, useState, useEffect, memo } from 'react';
import { Button, ButtonGroup, FormControl } from 'react-bootstrap'
import ReactTooltip from 'react-tooltip';
import { ProductosContext, ProductosType } from '../../Context/Producto';
import THFila, { THInput } from '../Componentes/THFila';
import { Detalles } from '../../Models/General';
import { numberFormat } from '../Formatos';
import { ControlText } from '../FormControl';
import { InputNumber } from '../Inputs';
interface Props {
    data: Detalles
    edit: (d: Detalles) => void
    remove: (iten: Detalles) => void
    dolar?: boolean
    venta?: boolean
    nota?: boolean
}

function ProductosVentas({ data, edit, remove, dolar = false, venta = false, nota = false }: Props) {
    const { findProVen, tasa, data: { User: { nivel } } } = useContext(ProductosContext) as ProductosType;
    const { codigo, descripcion, costo, cantidad, monto1, monto2, monto3, inventario } = findProVen(data, dolar);
    const [unidades, setUnidades] = useState(data.unidades);
    const [precio, setPrecio] = useState(data.precio);
    const [comentario, setComentario] = useState(data.comentario);
    const [open, setOpen] = useState(false);
    useEffect(() => edit({ ...data, unidades, precio, comentario }), [unidades, precio, comentario]);
    const className = () => {
        if (venta && !nota) {
            if ((monto1 - 0.01) >= precio)
                return 'bg-warning';
            if ((cantidad <= unidades) && (inventario !== 1) && (nota === undefined))
                return 'bg-danger';
        }
        return undefined;
    }
    const tip = () => {
        if (venta && !nota) {
            if (monto1 > precio)
                return 'ESTAS POR DEBAJO DEL PRECIO MINIMO';
            if ((cantidad < unidades) && (inventario !== 1))
                return 'NO TIENES DISPONIBILIDAD SUFICIENTE';
        }
        return undefined;
    }
    const pre = (dolar) ? precio / tasa : precio;
    return <>
        <tr className={className()} data-tip={tip()}>
            <THFila width='90px'>{codigo}</THFila>
            <THFila width='auto'>{descripcion}</THFila>
            <THInput width='120px'><InputNumber min={0.01} setData={setUnidades} value={unidades} /></THInput>
            <THInput width='120px'><FormControl style={{ textAlign: 'right' }} disabled value={(inventario === 1) ? 'NO APLICA' : numberFormat(cantidad)} /></THInput>
            <THInput width='180px' tip={'MINIMO: ' + numberFormat((nivel === 0) ? costo : monto1)}>
                <InputNumber list={costo + 'list'} setData={(p: number) => setPrecio((dolar) ? p * tasa : p)}
                    value={pre} min={(nivel === 0) ? costo : monto1} step='0.01' />
                <datalist id={costo + 'list'} key={costo + 'list' + dolar}>
                    <option>{monto1.toFixed(2)}</option>
                    <option>{monto2.toFixed(2)}</option>
                    <option>{monto3.toFixed(2)}</option>
                </datalist>
            </THInput>
            <THInput width='230px'><InputNumber disabled setData={() => { }} value={parseFloat(pre.toFixed(2)) * unidades} update="p" /></THInput>
            <THInput width='110px'>
                <ReactTooltip />
                <ButtonGroup>
                    {venta ? <></> : <Button variant="info" onClick={() => setOpen(!open)} data-tip="DEJAR NOTA">
                        <span className="fa fa-edit"></span>
                    </Button>}
                    <Button variant="danger" onClick={() => remove(data)} data-tip="QUITAR PRODUCTO">
                        <i className="fa fa-ban"></i>
                    </Button>
                </ButtonGroup>
            </THInput>
        </tr><tr></tr>
        {open ? <tr>
            <td colSpan={7}>
                <ControlText maxLength={250} before="NOTA INTERNA" setData={setComentario} value={comentario} />
            </td>
        </tr> : <tr></tr>}
    </>
}
export default memo(ProductosVentas);