
import React from 'react';
import { conNota } from '../../Models/Factura';
import ModalPrint from './ModalPrint';
import FilaVentas from '../../Utiles/Ventas/FilaVentas';
import { zfill } from '../../Utiles/Formatos';

export default class FilaNota extends FilaVentas {
    con = conNota();
    print = false;
    constructor(p: any) {
        super(p);
        this.Label = 'NOTA DE ENTREGA';
        this.tipo = 'Nota';
    }
    getModal() {
        let { codigo } = this.props.data;
        return <ModalPrint title={"IMPRIMIR " + this.Label + " N¬∞ " + zfill(codigo)}
            codigo={zfill(codigo)}
            key={'PrintDolar' + this.tipo + codigo}
            open={this.state.print} close={this.close.bind(this)} />
    }
    getStatus() {
        switch (this.props.data.status) {
            case 0:
                return <span className="badge badge-danger">CANCELADA</span>
            case 1:
                return <span className="badge badge-warning">ENTREGADO</span>
            case 2:
                return <span className="badge badge-success">FACTURADA</span>
        }
    }
    cancelado() {
        return 'NOTA DE ENTREGA N¬∞ ' + this.props.data.codigo + ' CANCELADA ESXITOSAMENTE';
    }
}