import React from 'react'
import { Button, Modal, Row, Col, FormGroup, FormLabel } from 'react-bootstrap'
import { toast } from 'react-toastify'
import { ComprasContext, ComprasType } from '../../Context/Compras'
import Proveedor, { conProveedor, ProveedorNull } from '../../Models/Proveedor'
import { ControlArea, ControlText } from '../../Utiles/FormControl'
interface Props {
    open: boolean
    Proveedor: Proveedor | null
    title: string
    close: () => void
    add?: (c: Proveedor) => void
}
interface State extends Proveedor {
    activeError: boolean;
}
class FormProveedor extends React.Component<Props, State> {
    error: any = [];
    con = conProveedor();
    constructor(p: Props) {
        super(p)
        this.setState = this.setState.bind(this);
        let state = p.Proveedor;
        if (state === null) state = ProveedorNull;
        this.state = { ...state, activeError: false };
    }
    render() {
        let { setState, state } = this
        return <Modal animation show={this.props.open} size='lg' centered onHide={this.props.close}>
            <Modal.Header className='bg-primary' closeButton>
                <h4 className='mr-auto'>{this.props.title}</h4>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <Col as={FormGroup} xs={12}>
                        <ControlText error={this.error} upper={true} mask='A-99999999-9' maxLength={20}
                            before="RIF" value={state.codigo} setData={setState} update="codigo" disabled={(this.props.Proveedor !== null)} />
                    </Col>
                    <Col as={FormGroup} xs={12}>
                        <ControlText error={this.error} upper={true} before="RAZON SOCIAL" maxLength={150}
                            value={state.nombre} setData={setState} update="nombre" />
                    </Col>
                    <Col as={FormGroup} xs={12}>
                        <ControlText  upper={true} before="CORREO" maxLength={150}
                            value={state.correo} setData={setState} update="correo" />
                    </Col>
                    <Col as={FormGroup} xs={12}>
                        <ControlText error={this.error} upper={true} maxLength={120}
                            before="TEL√âFONO" value={state.telefono} setData={setState} update="telefono" />
                    </Col>
                    <Col as={FormGroup} xs={12}>
                        <ControlText error={this.error} upper={true} before="PERSONA DE CONTACTO"
                            maxLength={50} value={state.contacto} setData={setState} update="contacto" />
                    </Col>
                    <Col as={FormGroup} xs={12}>
                        <FormLabel>DIRECCION</FormLabel>
                        <ControlArea error={this.error} upper={true} value={state.direccion} setData={setState} update="direccion" maxLength={300} />
                    </Col>
                </Row>
            </Modal.Body>
            <Modal.Footer>
                <Button className="float-right" onClick={this.get.bind(this)} variant="primary">
                    <span className="fa fa-floppy-disk" /> GUARDAR
                </Button>
            </Modal.Footer>
        </Modal>
    }
    isInvalid(s: string) {
        if (s === "")
            return true
        return false
    }
    get() {
        
        this.error = [];
        let procesar = true;
        if (this.state.codigo === "") {            
            this.error['codigo'] = 'DEBE COLOCAR EL DOCUMENTO LEGAL DEL PROVEEDOR';
            procesar=false;
        } if (this.state.nombre === "") {
            this.error['nombre'] = 'DEBE COLOCAR LA RAZON SOCIAL';
            procesar=false;
        } if (this.state.telefono === "") {
            this.error['telefono'] = 'DEBE COLOCAR NUMERO DE TELEFONO';
            procesar=false;
        }  if (this.state.contacto === "") {
            this.error['contacto'] = 'DEBE COLOCAR LA PERSONA DE CONTACTO';
            procesar=false;
        }  if (this.state.direccion === "") {
            this.error['direccion'] = 'DEBE COLOCAR UNA DIRECCION'; 
            procesar=false;
        } if(procesar) {
            if (this.props.Proveedor === null) {
                this.con.nuevo(this.state, this.action.bind(this),  this.setError.bind(this));
            } 
            else {
                this.con.actualizar(this.props.Proveedor.codigo,
                    this.state, this.action.bind(this),  this.setError.bind(this))
            } 
        } this.forceUpdate();
    }
    action(data: any) {
        const context = this.context as ComprasType;
        if (this.props.add) this.props.add(data);
        context.actProveedor();
        if (this.props.Proveedor === null) this.setState(ProveedorNull);
        this.props.close();
        
    }

    setError(err: any) {
        if (err.response) {
            let { errores } = err.response;
            errores.forEach((err: any) => {
                if (err.sql) {
                    err.sql.forEach((er: string) => toast(er, { type: toast.TYPE.ERROR }));
                } else {
                    Object.keys(err).forEach(t => this.error[t] = err[t])
                }
            })
            console.log(this.error);
            this.forceUpdate();
        }
    }
}

FormProveedor.contextType = ComprasContext;
export default FormProveedor;