
import React from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import ReactTooltip from 'react-tooltip';
import { GeneralFila, tipoData } from '../../Models/General';
import ModalPrint from '../Componentes/ModalPrint';
import ModalAlert from '../Componentes/ModalAlert';
import ModalDetalle from '../Componentes/ModalDetalle';
import { formatoFecha, numberFormat, zfill } from '../Formatos';
import { Conexion } from '../../Config/Coneccion';
import { conCotizacion } from '../../Models/Factura';
import THFila from '../Componentes/THFila';
import { toast } from 'react-toastify';
import EstadosLabel from '../Componentes/EstadosLabel';

interface Props {
    data: GeneralFila
    update?: () => void
}
interface State {
    print: boolean
    detalles: boolean
    aux: boolean
    eliminar: boolean
}
export default class FilaProto extends React.Component<Props, State>{
    con: Conexion = conCotizacion();
    Label: 'COTIZACI√ìN' | 'COMPRA' | 'FACTURA' | 'NOTA DE ENTREGA' | 'ORDEN DE COMPRA' = 'COTIZACI√ìN';
    tipo: tipoData = 'Nota';
    print = true;
    constructor(p: any) {
        super(p);
        this.state = { print: false, detalles: false, aux: false, eliminar: false };
    }
    render() {
        let cot = this.props.data,
            maxWidth = window.innerWidth - 890;
        return <tr >
            <th style={{ minWidth: "90px", width: "90px", maxWidth: "90px" }}>
                {this.tipo !== 'Cotizacion' ? <ModalDetalle key={'ModalDetalle' + this.tipo + cot.codigo}
                    tipo={this.tipo} titulo={"DETALLES DE LA " + this.Label + " N¬∞ " + zfill(cot.codigo)}
                    id={cot.codigo} open={this.state.detalles} close={this.close.bind(this)}
                /> : <></>}
                <Button data-tip="VER DETALLES" onClick={() => { this.setState({ detalles: true }) }} variant="link" >
                    {zfill(cot.codigo)}
                </Button>
            </th>
            <THFila width="90px" align='center'>{formatoFecha(cot.fecha)}</THFila>
            <th>
                <ReactTooltip />
                <Button style={{ maxWidth, whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}
                    data-class="toolbody" data-html="true" data-tip={this.tooltipCliente()} variant="link" >
                    {cot.nombre}
                </Button>
            </th>
            <THFila width="160px" align='right'>{numberFormat(cot.monto)}</THFila>
            <THFila width="100px" align='center'>{this.getStatus()}</THFila>
            <th style={{ minWidth: "135px", maxWidth: "135px" }}>
                {this.print ? <ModalPrint title={"IMPRIMIR " + this.Label + " N¬∞ " + zfill(cot.codigo)}
                    id={cot.codigo} tipo={this.tipo} key={'ModalPrint' + this.tipo + cot.codigo}
                    open={this.state.print} close={this.close.bind(this)} /> : <></>}
                <ModalAlert title={"ELIMINAR " + this.Label + " N¬∞ " + zfill(cot.codigo)} key={'ModalAlert' + this.tipo + cot.codigo}
                    msn={"ESTA SEGURO QUE ELIMINARA LA " + this.Label + " N¬∞" + cot.codigo + "?"}
                    open={this.state.eliminar} close={this.close.bind(this)} accion={this.cancelar.bind(this)} />
                {this.getModal()}
                {this.getButtons()}
            </th>
        </tr >
    }
    getModal() {
        return <></>;
    }
    getStatus() {
        return <EstadosLabel type={this.tipo} status={this.props.data.status} />
    }
    getButtons() {
        return <ButtonGroup className="float-right">
            <Button data-tip="VER IMPRESION" variant="primary"
                onClick={() => { this.setState({ print: true }) }}>
                <i className="fa fa-download"></i>
            </Button>
            {(this.props.data.status === 1) ?
                <Button data-tip="CANCELAR" variant="danger"
                    onClick={() => { this.setState({ eliminar: true }) }} >
                    <i className="fa fa fa-ban"></i>
                </Button> : <></>}
        </ButtonGroup>
    }
    tooltipCliente() {
        return ""
    }
    close() {
        this.setState({ print: false, detalles: false, aux: false, eliminar: false });
    }
    cancelar() {
        this.con.cancelar(this.props.data.codigo, () => {
            if (this.props.update) this.props.update();
            toast(this.cancelado(), { type: toast.TYPE.SUCCESS });
        })
        this.close();
    }
    cancelado() {
        return '';
    }
}