import React from 'react'
import { conCotizacion, conFactura, conNota } from '../Models/Factura'
import Cliente, { ClienteNull, conCliente } from '../Models/Cliente'
import { ProductosContext, ProductosType } from './Producto'
import { arrayToFilaGeneral, Estado, EstadoNull, GeneralFila } from '../Models/General'
import { formatoHora } from '../Utiles/Formatos'

interface State {
    estCliente: Estado
    estCotizacion: Estado
    estNota: Estado
    estFactura: Estado
    Facturas: GeneralFila[]
    Cotizaciones: GeneralFila[]
    Notas: GeneralFila[]
    Clientes: Cliente[]
}
export interface VentasType extends ProductosType, State {
    actualizar: () => void
    actCliente: () => void
    actCotizacion: () => void
    actNota: () => void
    actFactura: () => void
    findCliente: (c: string) => Cliente | undefined
    resetVenta: () => void
}
export const VentasContext = React.createContext<VentasType | null>(null)
class VentasProvider extends React.Component<any, State> {
    Facturas = 0;
    Cotizaciones = 0;
    Notas = 0;
    Clientes = 0;
    constructor(props: any) {
        super(props)
        this.state = {
            Facturas: [],
            Cotizaciones: [],
            Notas: [],
            Clientes: [],
            estCliente: EstadoNull,
            estCotizacion: EstadoNull,
            estNota: EstadoNull,
            estFactura: EstadoNull
        }
    }
    reset() {
        this.setState({
            estCliente: EstadoNull,
            estCotizacion: EstadoNull,
            estNota: EstadoNull,
            estFactura: EstadoNull
        })
    }
    render() {
        let seccionContext: ProductosType = this.context;
        this.set();
        return <VentasContext.Provider value={{
            ...this.state, ...seccionContext,
            actualizar: this.set.bind(this),
            actCliente: this.actCliente.bind(this),
            actCotizacion: this.actCotizacion.bind(this),
            actNota: this.actNota.bind(this),
            actFactura: this.actFactura.bind(this),
            findCliente: this.findCliente.bind(this),
            resetVenta: this.reset.bind(this)
        }}>
            {this.props.children}
        </VentasContext.Provider>
    }
    componentDidMount() {
        this.init();
    }
    init() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.length > 0) {
            if (seccionContext.data.Permisos.some(p => p === 'FACTURA'))
                conFactura().cargarListaCache((data) => { this.setState({ Facturas: arrayToFilaGeneral(data) }) });
            if (seccionContext.data.Permisos.some(p => (p === 'NOTA' || p === 'FACTURA')))
                conNota().cargarListaCache((data) => { this.setState({ Notas: arrayToFilaGeneral(data) }) });
            if (seccionContext.data.Permisos.some(p => (p === 'COTIZACION' || p === 'NOTA' || p === 'FACTURA')))
                conCotizacion().cargarListaCache((data) => { this.setState({ Cotizaciones: arrayToFilaGeneral(data) }) });
            if (seccionContext.data.Permisos.some(p => (p === 'COTIZACION' || p === 'NOTA' || p === 'FACTURA' || p === 'CLIENTE')))
                conCliente().cargarListaCache((data) => { this.setState({ Clientes: this.arrayToClientes(data) }) });
            this.set();
        } else {
            setTimeout(() => { this.init(); }, 1000);
        }
    }
    set() {
        this.actCliente();
        this.actCotizacion();
        this.actNota();
        this.actFactura();
    }
    actCliente() {
        let seccionContext: ProductosType = this.context;
        if (this.Clientes !== seccionContext.data.estados.Cliente) {
            this.Clientes = seccionContext.data.estados.Cliente;
            if (this.state.estCliente.status !== 2) {
                this.setState({ estCliente: { status: 2, hora: '...ACTUALIZANDO' } })
                conCliente().cargarListaWeb((data) => {
                    this.setState({
                        Clientes: this.arrayToClientes(data),
                        estCliente: { status: 3, hora: formatoHora(undefined) }
                    })
                });
            }
        }
    }
    actCotizacion() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.some(p => (p === 'COTIZACION' || p === 'NOTA' || p === 'FACTURA'))) {
            if (this.Cotizaciones !== seccionContext.data.estados.Cotizacion) {
                this.Cotizaciones = seccionContext.data.estados.Cotizacion;
                if (this.state.estCotizacion.status !== 2) {
                    this.setState({ estCotizacion: { status: 2, hora: '...ACTUALIZANDO' } })
                    conCotizacion().cargarListaWeb((data) => {
                        this.setState({
                            Cotizaciones: arrayToFilaGeneral(data),
                            estCotizacion: { status: 3, hora: formatoHora(undefined) }
                        })
                    });
                }
            }
        }
    }
    actNota() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.some(p => (p === 'NOTA' || p === 'FACTURA'))) {
            if (this.Notas !== seccionContext.data.estados.Nota) {
                this.Notas = seccionContext.data.estados.Nota;
                if (this.state.estNota.status !== 2) {
                    this.setState({ estNota: { status: 2, hora: '...ACTUALIZANDO' } })
                    conNota().cargarListaWeb((data) => {
                        this.setState({
                            Notas: arrayToFilaGeneral(data),
                            estNota: { status: 3, hora: formatoHora(undefined) }
                        })
                    });
                }
            }
        }
    }
    actFactura() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.some(p => p === 'FACTURA')) {
            if (this.Facturas !== seccionContext.data.estados.Factura) {
                this.Facturas = seccionContext.data.estados.Factura;
                if (this.state.estFactura.status !== 2) {
                    this.setState({ estFactura: { status: 2, hora: '...ACTUALIZANDO' } })
                    conFactura().cargarListaWeb((data) => {
                        this.setState({
                            Facturas: arrayToFilaGeneral(data),
                            estFactura: { status: 3, hora: formatoHora(undefined) }
                        })
                    });
                }
            }
        }
    }
    findCliente(c: string) {
        const cli = this.state.Clientes.find(cli => cli.codigo === c);
        if (cli !== undefined) return cli;
        if (this.state.estCliente.status !== 2) this.actCliente();
        return ClienteNull;
    }
    arrayToClientes(c: any[]): Cliente[] {
        return c.map(a => {
            return {
                codigo: a[0],
                nombre: a[1],
                telefono: a[2],
                correo: a[3],
                contacto: a[4],
                direccion: a[5],
                tipo_contribuyente: a[6],
                retencion: a[7],
                estatus: a[8]
            }
        })
    }
}
VentasProvider.contextType = ProductosContext;
export default VentasProvider;
export const SeccionConsumer = VentasContext.Consumer;