import React, { useState, useEffect } from "react";
import { Table } from "react-bootstrap";
import Paginacion from "./Paginacion";
interface Props {
    itens: any[]
    mostrar: number
    row: (d: any) => any
    col: number
    children: any
}
export default function Body({ children, col, row, mostrar, itens }: Props) {
    const [page, setPage] = useState(1);
    useEffect(() => setPage(1), [itens]);
    function body() {
        const init = (page - 1) * mostrar, fin = init + mostrar;
        const iten = itens.filter((e, i) => (i >= init && i < fin))
        return iten.map((iten: any) => row(iten));
    }
    return <Table striped hover >
        <thead>
            {children}
        </thead>
        <tbody key={'mostrar' + page}>
            {body()}
        </tbody>
        <tr>
            <td colSpan={col}>
                <Paginacion page={page} length={itens.length} mostrar={mostrar} setpage={setPage} />
            </td>
        </tr>
    </Table>
}