import React, { useContext } from 'react';
import { Route } from "react-router-dom"
import ListaClientes from '../App/Clientes';
import ListaCotizaciones from '../App/Cotizacion';
import NuevaCotizacion from '../App/Cotizacion/Nuevo';
import ListaFactura from '../App/Facturas';
import NuevaFactura from '../App/Facturas/Nuevo';
import ListaNotas from '../App/Nota';
import NuevaNota from '../App/Nota/Nuevo';
import { SeccionContext } from '../Context/Seccion';
import VentasProvider from '../Context/Ventas'
import UnAutorization from '../Utiles/Componentes/UnAutorization';
export default function MasterVentas() {
    return (<VentasProvider>
        <Route path="/cotizacion" component={Cotizacion} />
        <Route path="/factura" component={Factura} />
        <Route path="/nota" component={Nota} />
        <Route path="/clientes" component={Cliente} />
    </VentasProvider>);
}
function Cotizacion() {
    const contex = useContext(SeccionContext);
    if (contex?.data.Permisos.some(p => p === 'COTIZACION'))
        return <>
            <Route path="/cotizacion/lista" component={ListaCotizaciones} />
            <Route path="/cotizacion/nueva" component={NuevaCotizacion} />
        </>
    return <UnAutorization />
}
function Factura() {
    const contex = useContext(SeccionContext);
    if (contex?.data.Permisos.some(p => p === 'FACTURA'))
        return <>
            <Route path="/factura/lista" component={ListaFactura} />
            <Route path="/factura/nueva" component={NuevaFactura} />
        </>
    return <UnAutorization />
}
function Nota() {
    const contex = useContext(SeccionContext);
    if (contex?.data.Permisos.some(p => p === 'NOTA'))
        return <>
            <Route path="/nota/lista" component={ListaNotas} />
            <Route path="/nota/nueva" component={NuevaNota} />
        </>
    return <UnAutorization />
}
function Cliente() {
    const contex = useContext(SeccionContext);
    if (contex?.data.Permisos.some(p => p === 'CLIENTE'))
        return <ListaClientes />
    return <UnAutorization />
}