import { GeneralFila } from "../Models/General"
import { Producto } from "../Models/Productos";
import { zfill } from "./Formatos";
export type filaLabel = "codigo" | "fecha" | "nombre" | "telefono" | "nota" | "tasa" | "monto" | "usuario" | "status";

export function busqueda<p>(itens: any[], encontar: string, bucarLabel: p[]) {
    if (encontar !== "") {
        return itens.filter(iten => {
            let en = encontar.split(" "), pasa = false;
            bucarLabel.forEach(label => {
                let considencias = 0;
                en.forEach(e => {
                    if (isNaN(iten[label])) {
                        if (iten[label].toUpperCase().indexOf(e) > -1) { considencias++ }
                    } else {
                        if (zfill(parseFloat(iten[label])).indexOf(e) > -1) { considencias++ }
                    }
                })
                if (en.length === considencias) {
                    pasa = true
                }
            })
            return pasa
        })
    }
    return itens;
}
export function filtro<l>(itens: any[], value: number, Label: l) {
    if (value !== -1)
        return itens.filter(iten => {
            return iten[Label] === value;
        })
    return itens;
}
export function filtrarProducto(itens: GeneralFila[], values: Producto[]) {
    if (values.length === 0) return itens;
    return itens.filter(i => {
        let pasa: string[] = [];
        i.detalles.forEach(d => { if (values.some(v => v.codigo === d)) pasa.push(d); });
        return (pasa.length === values.length);
    });
}