import React from 'react';
import MenuIten from './MenuIten'
import { Link } from "react-router-dom"
import { SeccionContext } from '../Context/Seccion'

class Menu extends React.Component {
    render() {
        const p = this.context.data.Permisos;
        return <aside className="main-sidebar sidebar-dark-primary">
            <Link to="/" className="brand-link bg-success" style={{ width: '100%', height: '57px' }}>
                <img width={33} height={33} src="/logos/logo.svg" alt="logo" className="brand-image img-circle" />
                <img src="/logos/texto.svg" alt="letras" className="brand-image brand-text" style={{ transitionDelay: '.2s' }} />
            </Link>
            <div className="sidebar">
                <nav className="mt-2">
                    <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
                        <li className="nav-item">
                            <Link to="/" className="nav-link">
                                <i className="nav-icon fa fa-home"></i> <p>INICIO</p>
                            </Link>
                        </li>
                        <MenuIten p={p} to="/orden/lista" permiso="ORDEN">
                            <i className="nav-icon fa fa-paperclip"></i> <p style={{ width: "200px" }}> ORDENES DE COMPRA </p>
                        </MenuIten>
                        <MenuIten p={p} to="/compra/lista" permiso="COMPRA">
                            <i className="nav-icon fa fa-shopping-cart"></i> <p> COMPRAS </p>
                        </MenuIten>
                        <MenuIten p={p} to="/cotizacion/lista" permiso="COTIZACION">
                            <i className="nav-icon fa fa-calculator"></i> <p> COTIZACIONES </p>
                        </MenuIten>
                        <MenuIten p={p} to="/nota/lista" permiso="NOTA">
                            <i className="nav-icon fa fa-paper-plane"></i> <p> NOTAS DE ENTREGA </p>
                        </MenuIten>
                        <MenuIten p={p} to="/factura/lista" permiso="FACTURA">
                            <i className="nav-icon fa fa-tag"></i> <p> FACTURAS </p>
                        </MenuIten>
                        <MenuIten p={p} to="/proveedores" permiso="PROVEEDOR">
                            <i className="nav-icon fa fa-truck"></i> <p> PROVEEDORES</p>
                        </MenuIten>
                        <MenuIten p={p} to="/clientes" permiso="CLIENTE">
                            <i className="nav-icon fa fa-user"></i><p> CLIENTES</p>
                        </MenuIten>
                        <MenuIten p={p} to="/producto" permiso="PRODUCTO">
                            <i className="nav-icon fa fa-laptop"></i> <p> INVENTARIO</p>
                        </MenuIten>
                        <MenuIten p={p} to="/producto/ajuste/lista" permiso="AJUSTE">
                            <i className="nav-icon fa fa-th-large"></i> <p>AJUSTES</p>
                        </MenuIten>
                        <MenuIten p={p} to="/usuarios" permiso="USUARIO">
                            <i className="nav-icon fa fa-users"></i><p> USUARIOS</p>
                        </MenuIten>
                        <MenuIten p={p} to="/reporte" permiso="REPORTE">
                            <i className="nav-icon fa fa-chart-line"></i><p> REPORTES</p>
                        </MenuIten>
                        <MenuIten p={p} to="/config" permiso="CONFIG">
                            <i className="nav-icon fa fa-wrench"></i><p> CONFIGURACION</p>
                        </MenuIten>
                    </ul>
                </nav>
            </div>
        </aside>
    }
}
Menu.contextType = SeccionContext;
export default Menu;