
import React from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import { conCompra } from '../../Models/Compra';
import FilaCompras from '../../Utiles/Compra/FilaCompras';
import RegistrarFactura from './RegistrarFactura';

export default class FilaCompra extends FilaCompras {
    con = conCompra();
    constructor(p: any) {
        super(p);
        this.Label = 'COMPRA';
        this.tipo = 'Compra';
    }
    getModal() {
        let { codigo } = this.props.data;
        return <>
            <RegistrarFactura id={codigo}
                key={'RegistrarFactura' + this.tipo + codigo}
                close={this.close.bind(this)}
                open={this.state.aux}
            />
        </>
    }
    getStatus() {
        switch (this.props.data.status) {
            case 0:
                return <span className="badge badge-danger">CANCELADA</span>
            case 1:
                return <span className="badge badge-warning">SIN FACTURA</span>
            case 2:
                return <span className="badge badge-success">FACTURADA</span>
        }
    }
    getButtons() {
        return <ButtonGroup className="float-right">
            {(this.props.data.status === 1) ?
                <Button data-tip="REGISTAR FACTURA" variant="outline-dark"
                    onClick={() => { this.setState({ aux: true }) }} >
                    <i className="fa fa-edit"></i>
                </Button> : <></>}
            <Button data-tip="VER IMPRECION" variant="primary"
                onClick={() => { this.setState({ print: true }) }}>
                <i className="fa fa-download"></i>
            </Button>
            {(this.props.data.status > 0) ?
                <Button data-tip="CANCELAR" variant="danger"
                    onClick={() => { this.setState({ eliminar: true }) }} >
                    <i className="fa fa-ban"></i>
                </Button> : <></>}
        </ButtonGroup>
    }
    cancelado() {
        return 'COMPRA N¬∞ ' + this.props.data.codigo + ' CANCELADA ESXITOSAMENTE';
    }
}