import React from "react"
import { Button, InputGroup } from "react-bootstrap";
import { toast } from "react-toastify";
import { ConConfig } from "../../Models/Config";
import Area from "../../Utiles/Charts/Area";
import { formatoFecha, formatoHora, fechaToNumber } from "../../Utiles/Formatos";
import { InputNumber } from "../../Utiles/Inputs";
type dolar = { fecha: string, valor: number };
export default class Dolar extends React.Component<{ p: string[] }, { dolares: dolar[], valor: number, fecha: string }>{
    constructor(p: any) {
        super(p);
        this.state = { dolares: [], valor: 0, fecha: '' };
    }
    render() {
        const { state: { valor, fecha }, props: { p } } = this;
        return <div className="Dolar">
            <div style={{ display: "flex" }}>
                <InputGroup style={{ width: "calc(100% - 250px)" }}>
                    <InputGroup.Prepend>
                        <InputGroup.Text>TASA DE CAMBIO</InputGroup.Text>
                        <InputGroup.Text><i className="fa fa-usd"></i></InputGroup.Text>
                    </InputGroup.Prepend>
                    <InputNumber min={0} value={valor} setData={this.setState.bind(this)} update='valor' disabled={!p.some(d => d === 'DOLARES')} />
                    {p.some(d => d === 'DOLARES') ?
                        <InputGroup.Append>
                            <Button variant='primary' onClick={this.actualizar.bind(this)}>
                                <i className="fa fa-refresh"></i> ACTUALIZAR
                            </Button>
                        </InputGroup.Append> : <></>}
                </InputGroup>
                <div style={{ fontSize: '12px', width: "250px", paddingLeft: '15px' }}>
                    <strong>ACTUALIZADO:</strong> {formatoFecha(fecha)}<br />
                    <strong>HORA:</strong> {formatoHora(fecha)}
                </div>
            </div>
            <Area label={['FECHA Y HORA', 'PRECIO']} values={this.get()} title="FLUCTUACIONES DEL DOLAR" hAxisTitle="FECHA Y HORA" />
        </div>
    }
    actualizar() {
        const con = ConConfig('');
        const { valor } = this.state;
        if (valor !== 0) con.post('/api/Dolares/set', { valor }, () => { this.send() });
        else toast('LA TASA DE CAMBIO NO PUEDE SER 0', { type: toast.TYPE.ERROR });
    }
    get() {
        return this.state.dolares.map(d => [formatoFecha(d.fecha) + ' ' + formatoHora(d.fecha), d.valor])
    }
    async send() {
        try {
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/api/Dolares/formatter/last', { headers: { 'Authorization': 'Bearer ' + token } });
            let dolares: dolar[] = await response.json();
            let valor = 0;
            let fecha = '1999-01-01';
            dolares.forEach(d => {
                if (fechaToNumber(d.fecha) > fechaToNumber(fecha)) {
                    valor = d.valor;
                    fecha = d.fecha;
                }
            })
            this.setState({ dolares, valor, fecha });
        } catch (error) {
            console.log(error);
            this.setState({ dolares: [] });
        }
    }
    componentDidMount() { this.send() }
}