import React from 'react'
import { Button, Modal } from 'react-bootstrap'
import { ConConfig } from '../../Models/Config'
import { ControlNumber } from '../../Utiles/FormControl'
export default class NuevoEquilibrio extends React.Component<{ open: boolean, close: () => void }, { value: number }> {
    state = { value: 0 }
    render() {
        let { setState, state: { value }, props: { open, close } } = this
        return <Modal animation show={open} size='lg' centered onHide={close}>
            <Modal.Header className='bg-primary' closeButton>
                <h4 className='mr-auto'>NUEVO PUNTO DE EQUILIBRIO</h4>
            </Modal.Header>
            <Modal.Body>
                <ControlNumber before='INGRESE EL NUEVO PUNTO DE EQUILIBRIO'
                    value={value} setData={setState.bind(this)} update='value' />
            </Modal.Body>
            <Modal.Footer>
                <Button className="float-right" onClick={this.get.bind(this)} variant="primary">
                    <span className="fa fa-floppy-disk" /> GUARDAR
                </Button>
            </Modal.Footer>
        </Modal>
    }
    get() {
        const con = ConConfig('');
        let { state: { value }, props: { close } } = this
        con.post('/api/Factura/equilibrio/', { pto: value }, () => { close() })
    }
}