import { Conexion } from "../Config/Coneccion";
import { CondicionesData, GeneralData, getSubtotal, sentDetalles, getCosto, condicionesNull, generalNull, Detalles } from "./General";

export interface Cotizacion extends CondicionesData {
    cod_cliente: string
    plantilla: number
    dolar: boolean
}
export const CotizacionNull: Cotizacion = {
    cod_cliente: '',
    plantilla: 0,
    dolar: false,
    ...condicionesNull,
}
export function sentCotizacion(e: Cotizacion, iva: number) {
    let subtotal = getSubtotal(e.detalles),
        impuesto = subtotal * (iva / 100),
        total = subtotal + impuesto;
    let { cod_cliente, validez, tiempo_entrega, forma_pago, nota, plantilla } = e;
    return {
        cod_cliente,
        plantilla,
        validez,
        tiempo_entrega,
        forma_pago,
        nota,
        total,
        impuesto,
        subtotal,
        detalles: sentDetalles(e.detalles)
    }
}
export interface Nota extends GeneralData {
    cod_cliente: string
}
export function sentNota(e: Nota, porc_impuesto: number) {
    let subtotal = getSubtotal(e.detalles),
        impuesto = subtotal * (porc_impuesto / 100),
        total = subtotal + impuesto;

    let { cod_cliente, nota, id_cotizacion } = e;
    return {
        id_cotizacion,
        cod_cliente,
        impuesto,
        total,
        nota,
        detalles: sentDetalles(e.detalles)
    }
}
export interface Factura extends GeneralData {
    cod_cliente: string
    condicion: string
    id_nota?: number
    id_cotizacion?: number
}
export const FacturaNull: Factura = {
    cod_cliente: '',
    condicion: '',
    ...generalNull,
}
export function sentFactura(e: Factura, porc_impuesto: number) {
    let subtotal = getSubtotal(e.detalles),
        costo = getCosto(e.detalles),
        impuesto = subtotal * (porc_impuesto / 100),
        total = subtotal + impuesto;

    let { cod_cliente, nota, condicion, id_nota, id_cotizacion } = e;
    return {
        id_nota,
        id_cotizacion,
        cod_cliente,
        condicion,
        porc_impuesto,
        costo,
        impuesto,
        subtotal,
        total,
        nota,
        detalles: sentDetalles(e.detalles)
    }
}
class CotizacionConexion extends Conexion {
    clase = 'Cotizacion';
    persistencia = 1000;
}
export function conCotizacion() {
    return new CotizacionConexion();
}
class NotaConexion extends Conexion {
    clase = 'Nota';
    persistencia = 1000;
    commadUpdate() {
        this.updater(this.clase);
        this.updater('Producto');
    }
}
export function conNota() {
    return new NotaConexion();
}
class FacturaConexion extends Conexion {
    clase = 'Factura';
    persistencia = 1000;
    commadUpdate() {
        this.updater(this.clase);
        this.updater('Producto');
    }
}
export function conFactura() {
    return new FacturaConexion();
}
export function updatePrecio(data: Cotizacion, tasa2: number, dolar: boolean): any {
    let detalles: Detalles[] = [];
    const id_cotizacion = data.codigo;
    const { cod_cliente } = data;
    if (data.tasa === 0 || dolar) detalles = data.detalles
    else detalles = data.detalles.map(({ codigo, departamento, descripcion, estatus, enser, tipo, costo, precio1, precio2,
        precio3, cantidad, unidad, exento, fecha_creacion, tasa, medida, inventario, unidades, comentario, precio, }) => {
        return {
            codigo, departamento, descripcion, estatus, enser, tipo, costo, precio1, precio2, precio3,
            cantidad, unidad, exento, fecha_creacion, tasa, medida, inventario, unidades, comentario,
            precio: parseFloat((precio * tasa2 / data.tasa).toFixed(2)),
        };
    });
    return { id_cotizacion, cod_cliente, detalles };
}