import React from 'react';
import { Button, Col, Card } from 'react-bootstrap';
import { ConConfig } from '../../Models/Config';
import { ControlText } from '../../Utiles/FormControl';
import { InputArea } from '../../Utiles/Inputs';
interface State {
    nombre: string
    numero_fiscal: string
    direccion: string
    telefono: string
    correo: string
    web: string
    pago: string
    logo: string
    eslogan: string
}
export default class Empresa extends React.Component<any, State> {
    con = ConConfig('empresa');
    state = { nombre: '', numero_fiscal: '', direccion: '', telefono: '', correo: '', web: '', pago: '', logo: '', eslogan: '' };
    render() {
        let { nombre, numero_fiscal, direccion, telefono, correo, web, pago, logo, eslogan } = this.state;
        let img = logo.length < 100 ? '/public/imagenes/' + logo : logo;
        let setState = this.setState.bind(this);
        return (<Card>
            <Card.Header className='bg-info'>
                <h4 className='mr-auto'><i className="fa fa-edit"></i> CONFIGURACION DE REGION </h4>
            </Card.Header>
            <Card.Body>
                <Col md={12}>
                    <ControlText before="NUMERO FISCAL" value={numero_fiscal} setData={setState} update="numero_fiscal" />
                </Col>
                <Col md={12}><br />
                    <ControlText before="NOMBRE" value={nombre} setData={setState} update="nombre" />
                </Col>
                <Col md={12}><br />
                    <ControlText before="CORREO" value={correo} setData={setState} update="correo" />
                </Col>
                <Col md={12}><br />
                    <ControlText before="TELEFONO" value={telefono} setData={setState} update="telefono" />
                </Col>
                <Col md={12}><br />
                    <ControlText before="WEB" value={web} setData={setState} update="web" />
                </Col>
                <Col md={12}><br />
                    <label className="control-label">DIRECCION:</label>
                    <InputArea rows={3} setData={this.setState.bind(this)} value={direccion} update="direccion" />
                </Col>
                <Col md={12}><br />
                    <label className="control-label">INFORMACION DE PAGO:</label>
                    <InputArea rows={3} setData={this.setState.bind(this)} value={pago} update="pago" />
                </Col>
                <Col md={12}><br />
                    <label className="control-label">ESLOGAN:</label>
                    <InputArea rows={3} setData={this.setState.bind(this)} value={eslogan} update="eslogan" />
                </Col>
                <Col md={12}><br />
                    <label className="control-label">LOGO:</label>
                    <label style={{ width: '300px' }}>
                        <img src={img} width="100%" alt='logo' />
                        <input style={{ display: "none" }} onChange={this.setImage.bind(this)} type="file" />
                    </label>
                </Col>
                <Button className="float-right" onClick={this.guardar.bind(this)} variant="primary">
                    <span className="fa fa-floppy-o" /> Guardar
                </Button>
            </Card.Body>
        </Card>)
    }
    setImage(e: React.ChangeEvent<HTMLInputElement>) {
        let t = this, f = e.currentTarget.files;
        let reader = new FileReader()
        reader.onloadend = function () {
            t.setState({ logo: reader.result as string })
        }
        if (f != null) reader.readAsDataURL(f[0]);
    }
    componentDidMount() {
        this.con.pull(this.setState.bind(this));
    }
    guardar() {
        this.con.push(this.state, this.setState.bind(this));
    }
}