import React from 'react'
import { conCompra, conOrden } from '../Models/Compra'
import { arrayToFilaGeneral, Estado, EstadoNull, GeneralFila } from '../Models/General'
import Proveedor, { conProveedor, ProveedorNull } from '../Models/Proveedor'
import { formatoHora } from '../Utiles/Formatos'
import { ProductosContext, ProductosType } from './Producto'
interface State {
    estOrdenes: Estado
    estCompras: Estado
    estProveedores: Estado
    Ordenes: GeneralFila[]
    Compras: GeneralFila[]
    Proveedores: Proveedor[]
}
export interface ComprasType extends ProductosType, State {
    actualizar: () => void
    actCompra: () => void
    actOrden: () => void
    actProveedor: () => void
    findProveedor: (p: string) => Proveedor | undefined
    resetCompra: () => void
}
export const ComprasContext = React.createContext<ComprasType | null>(null)
class ComprasProvider extends React.Component<any, State> {
    Ordenes = 0;
    Compras = 0;
    Proveedores = 0;
    constructor(props: any) {
        super(props)
        this.state = {
            Ordenes: [],
            Compras: [],
            Proveedores: [],
            estOrdenes: EstadoNull,
            estCompras: EstadoNull,
            estProveedores: EstadoNull
        }
    }
    render() {
        let seccionContext: ProductosType = this.context;
        this.set();
        return <ComprasContext.Provider value={{
            ...this.state, ...seccionContext,
            actualizar: this.set.bind(this),
            actCompra: this.actCompra.bind(this),
            actOrden: this.actOrden.bind(this),
            actProveedor: this.actProveedor.bind(this),
            findProveedor: this.findProveedor.bind(this),
            resetCompra: this.reset.bind(this)
        }}>
            {this.props.children}
        </ComprasContext.Provider>
    }
    reset() {
        this.setState({
            estOrdenes: EstadoNull,
            estCompras: EstadoNull,
            estProveedores: EstadoNull
        })
    }
    componentDidMount() {
        this.init();
    }
    init() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.length > 0) {
            if (seccionContext.data.Permisos.some(p => (p === 'COMPRA')))
                conCompra().cargarListaCache((data) => {
                    this.setState({ Compras: arrayToFilaGeneral(data) })
                });
            conOrden().cargarListaCache((data) => { this.setState({ Ordenes: arrayToFilaGeneral(data) }) });
            conProveedor().cargarListaCache((data) => { this.setState({ Proveedores: this.arrayToProveedor(data) }) });
        } else {
            setTimeout(() => { this.init(); }, 1000);
        }
    }
    set() {
        this.actCompra();
        this.actOrden();
        this.actProveedor();
    }
    actCompra() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.some(p => (p === 'COMPRA'))) {
            if (this.Compras !== seccionContext.data.estados.Compra) {
                this.Compras = seccionContext.data.estados.Compra;
                if (this.state.estCompras.status !== 2) {
                    this.setState({ estCompras: { status: 2, hora: '...ACTUALIZANDO' } })
                    conCompra().cargarListaWeb((data) => {
                        this.setState({
                            Compras: arrayToFilaGeneral(data),
                            estCompras: { status: 3, hora: formatoHora(undefined) }
                        })
                    });
                }
            }
        }
    }
    actOrden() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.some(p => (p === 'COMPRA' || p === 'ORDEN'))) {
            if (this.Ordenes !== seccionContext.data.estados.Orden) {
                this.Ordenes = seccionContext.data.estados.Orden;
                if (this.state.estOrdenes.status !== 2) {
                    this.setState({ estOrdenes: { status: 2, hora: '...ACTUALIZANDO' } })
                    conOrden().cargarListaWeb((data) => {
                        this.setState({
                            Ordenes: arrayToFilaGeneral(data),
                            estOrdenes: { status: 3, hora: formatoHora(undefined) }
                        })
                    });
                }
            }
        }
    }
    actProveedor() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.some(p => p === 'COMPRA' || p === 'ORDEN' || p === 'PROVEEDOR')) {
            if (this.Proveedores !== seccionContext.data.estados.Proveedor) {
                this.Proveedores = seccionContext.data.estados.Proveedor;
                if (this.state.estProveedores.status !== 2) {
                    this.setState({ estProveedores: { status: 2, hora: '...ACTUALIZANDO' } })
                    conProveedor().cargarListaWeb((data) => {
                        this.setState({
                            Proveedores: this.arrayToProveedor(data),
                            estProveedores: { status: 3, hora: formatoHora(undefined) }
                        })
                    });
                }
            }
        }
    }
    findProveedor(p: string) {
        const pro = this.state.Proveedores.find(Pro => Pro.codigo === p);
        if (pro !== undefined) return pro;
        if (this.state.estProveedores.status !== 2) this.actProveedor();
        return ProveedorNull;

    }
    arrayToProveedor(p: any[]): Proveedor[] {
        return p.map(a => {
            return {
                codigo: a[0],
                nombre: a[1],
                telefono: a[2],
                correo: a[3],
                contacto: a[4],
                direccion: a[5],
                estatus: a[6]
            }
        })
    }

}
ComprasProvider.contextType = ProductosContext;
export default ComprasProvider;
export const SeccionConsumer = ComprasContext.Consumer;