import { Conexion } from "../Config/Coneccion";

export default interface Proveedor {
    codigo: string
    nombre: string
    telefono: string
    correo: string
    contacto: string
    direccion: string
    estatus: number
}
export var ProveedorNull: Proveedor = {
    codigo: "",
    nombre: "",
    telefono: "",
    correo: "",
    contacto: "",
    direccion: "",
    estatus: 1
}
class ProveedorConexion extends Conexion {
    clase = 'Proveedor';
    persistencia = 1000;
}
export function conProveedor() {
    return new ProveedorConexion();
}