import React from 'react'
import db from '../Config/Firebase'
import { ConConfig } from '../Models/Config'
import { Estado, EstadoNull } from '../Models/General'
import Usuario, { conUsuario, userNull, UsuarioFila } from '../Models/Usuario'
import { formatoHora } from '../Utiles/Formatos'
import { ref as getRef, onValue } from "firebase/database";
import { setRif } from '../Config/rif';

interface State {
    cargando: boolean,
    fecha: string
    User: Usuario
    Permisos: Array<any>
    seccion: boolean
    usuarios: UsuarioFila[]
    estUsuarios: Estado,
    rif: string,
    estados: {
        Cliente: number,
        Compra: number,
        Config: number,
        Cotizacion: number,
        Departamento: number,
        Dolares: number,
        Factura: number,
        Nota: number,
        Orden: number,
        Plantilla: number,
        Producto: number,
        Proveedor: number,
        Tipo: number,
        Unidad: number,
        Usuario: number,
        Moneda: number,
        Ajuste: number,
    }
}
export interface SeccionType {
    data: State,
    logout: () => void,
    usuarios: UsuarioFila[]
    user: () => void,
    atcUsuario: () => void
}

export const SeccionContext = React.createContext<SeccionType | null>(null)
export class SeccionProvider extends React.Component<any, State> {
    interval: any;
    usuarios = 0;
    constructor(props: any) {
        super(props)
        this.state = {
            cargando: true,
            User: userNull,
            fecha: '',
            Permisos: [],
            seccion: true,
            usuarios: [],
            estUsuarios: EstadoNull,
            rif: '',
            estados: {
                Cliente: 1,
                Compra: 1,
                Config: 1,
                Cotizacion: 1,
                Departamento: 1,
                Dolares: 1,
                Factura: 1,
                Nota: 1,
                Orden: 1,
                Plantilla: 1,
                Producto: 1,
                Proveedor: 1,
                Tipo: 1,
                Unidad: 1,
                Usuario: 1,
                Moneda: 1,
                Ajuste: 1,
            }
        }
    }
    render() {
        this.atcUsuario();
        return <SeccionContext.Provider value={{
            data: this.state,
            logout: this.logout.bind(this),
            usuarios: this.state.usuarios,
            user: this.user.bind(this),
            atcUsuario: this.atcUsuario.bind(this),
        }}>
            {this.props.children}
        </SeccionContext.Provider>
    }
    async logout() {
        try {
            //solicitar nuevos datos
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/api/Auth/close', { headers: { 'Authorization': 'Bearer ' + token } });
            localStorage.removeItem('Authorization');
            await response.json();
            //cargar nuevos datos
            this.user();
            //Actualizadar nuevos datos en cache
        } catch (err) {
            console.log(err);
        }
    }
    async user() {
        try {
            //solicitar nuevos datos
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/api/Auth/me', { headers: { 'Authorization': 'Bearer ' + token } });
            let data: Usuario = await response.json();
            //cargar nuevos datos
            this.setState({
                User: data,
                Permisos: data.permisos,
                seccion: (data.id_usuario !== 0),
                cargando: false
            })
            //Actualizadar nuevos datos en cache
        } catch (err) {
            console.log(err);
        }
    }
    atcUsuario() {
        if (this.usuarios !== this.state.estados.Usuario) {
            this.usuarios = this.state.estados.Usuario;
            if (this.state.estUsuarios.status !== 2) {
                this.setState({ estUsuarios: { status: 2, hora: '...ACTUALIZANDO' } })
                conUsuario().cargarListaWeb(usuarios => this.setState({
                    usuarios,
                    estUsuarios: { status: 3, hora: formatoHora(undefined) }
                }));
            }
        }
    }
    componentDidUpdate(p: any, s: State) {
        const { state: { seccion, cargando, rif }, interval } = this;
        if ((interval === undefined) && seccion && !cargando)
            this.initUpdater();
        if (rif !== '' && s.rif !== rif) {
            setRif(rif);
            const starCountRef = getRef(db, rif);
            onValue(starCountRef, snap => {
                this.setState({ estados: snap.val() })
            });
        }
    }
    componentDidMount() {
        ConConfig('empresa').pull(({ numero_fiscal }: { numero_fiscal: string }) => this.setState({ rif: numero_fiscal }))
        this.user();
    }
    initUpdater() {
        this.atcUsuario();
        conUsuario().cargarListaCache(usuarios => this.setState({ usuarios }));
        this.interval = setInterval(() => ConConfig('estado').pull(data => {
            // if (JSON.stringify(this.state.estados) !== JSON.stringify(data))
            //     this.setState({ estados: data })
        }), 5 * 1000)
    }
    setRif() {
    }
    componentWillUnmount() { clearInterval(this.interval); }

}
export const SeccionConsumer = SeccionContext.Consumer;
